<template>
  <div class="container">
    <div class="back" v-if="isHaierApp" @click="goBack"></div>
    <div class="logo">
      <img src="@/assets/images/logo1.png" alt="" />
      <p>创全球首个物联网社群交互模式</p>
    </div>
    <div class="download">
      <div
        v-if="(isHaierApp && isIos) || !isHaierApp"
        class="ios btn"
        @click="downloadBtn('ios')"
      >
        <div class="box">
          <div class="icon">
            <img src="@/assets/images/icon-ios.png" alt="" />
          </div>
          <div class="text">iPhone版下载</div>
        </div>
      </div>
      <div
        v-if="(isHaierApp && isAndriod) || !isHaierApp"
        class="android btn"
        @click="downloadBtn('android')"
      >
        <div class="box">
          <div class="icon">
            <img src="@/assets/images/icon-andriod.png" alt="" />
          </div>
          <div class="text">Android版下载</div>
        </div>
      </div>
    </div>
    <div class="version-info">
      <p>版本号：5.4.5  更新时间：2021.09.03 </p>
    </div>
    <div class="continue">
      <div class="dev-info" v-if="!isHaierApp">
        开发者：海尔集团电子商务有限公司 点击查看《<a href="https://m.ehaier.com/v3/mstore/sg/helpDetail.html?id=1198" target="_blank">App隐私政策权</a>》
      </div>
      <div class="dev-info" v-else>
        开发者：海尔集团电子商务有限公司 点击查看《<div class="custom-href" @click="toAppQy">App隐私政策权</div>》
      </div>
      <div v-if="!isHaierApp" class="text" @click="toSG">继续访问网页版</div>
    </div>
  </div>
</template>

<script>
import semver from '@/utils/semver'
import UplusApi from '@uplus/uplus-api';
const instance = new UplusApi()
instance.initDeviceReady()

export default {
  data() {
    return {
      isHaierApp: navigator.userAgent.indexOf('App/Uplus') > -1 || navigator.userAgent.indexOf('App/SyBird') > -1,
      isAndriod: navigator.userAgent.indexOf('Android') > -1,
      isIos: navigator.userAgent.indexOf('iPhone') > -1,
    };
  },
  mounted() {
    this.$util.testInstallApp('shunguang://', () => {
      location.href = 'shunguang://'
    })
  },
  methods: {
    downloadBtn(os) {
      if (os === 'ios') {
        location.href = 'https://itunes.apple.com/cn/app/shun-guang-wei-dian/id1035160364?mt=8'
      } else if (os === 'android') {
        if (this.$util.isHaierApp()) {
          instance.initDeviceReady().then(() => {
            instance.upAppinfoModule.getAppInfo().then(result => {
              let appVersion = result.retData.appVersion
              let osVersion = result.retData.OSversion
              console.log(appVersion, osVersion, 'appVersion')
              if (appVersion && semver.gte(appVersion, '7.3.0')) {
                this.$util.openNewWindow('https://sg-apk-store.oss-cn-beijing.aliyuncs.com/shunguang_android.apk?container_type=4', true, true)
              } else {
                location.href = 'https://sg-apk-store.oss-cn-beijing.aliyuncs.com/shunguang_android.apk'
              }
            })
          })
        } else {
          location.href = 'https://sg-apk-store.oss-cn-beijing.aliyuncs.com/shunguang_android.apk'
        }
      }
    },
    toSG() {
      window.open('https://m.ehaier.com/sgmobile/');
    },
    toAppQy() {
      instance.initDeviceReady().then(() => {
        instance.upVdnModule.goToPage({ url: 'https://m.ehaier.com/sgmobile/apphelp' });
      });
    },
    goBack() {
      instance.initDeviceReady().then(() => {
        instance.closeH5ContainerView();
      });
    }
  }
};
</script>

